.banner-container {
    background-position: center center;
    background-size: cover;
    position: relative;
    z-index: 1;
}

.banner-container::before {
    position: absolute;
    content: "";
    background: #000000;
    opacity: 0.3;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}

.homeHeaderSlider .slick-list,
.homeHeaderSlider .slick-list .slick-track,
.homeHeaderSlider .slick-list .slick-track .slick-slide,
.homeHeaderSlider .slick-list .slick-track .slick-slide>div {
    height: 100%;
}